@import '../../scss/variables';

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;

  .cards {
    background-color: $cardNavBgColor;
    margin: 1rem;
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.7);
    position: relative;
  }
  .title {
    color: $backgroundColor;
    font-size: 1.5rem;
    padding: 1rem;
  }
  img {
    width: 500px;
    height: 350px;
    object-fit: cover;
    filter: grayscale(0%);
    transition: filter 0.5s;
    &:hover {
      filter: grayscale(100%);
    }
  }
  .card-over {
    color: white;
    position: absolute;
    bottom: 0;
    left: 0;
    font-size: 1.5rem;
    padding: 1rem;
    transform: translateY(100%);
    z-index: 1;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.5);
    transition: transform 0.8s ease-in-out;
    border-radius: 10px;
  }
  .cards:hover .card-over {
    opacity: 1;
    transform: translateY(0);
  }
}
